<template>
  <div class="test-container">
    <div class="data" style="margin-bottom: 20px;display: flex;justify-content: space-around">
      <div>经度：{{longitude}}</div>
      <div>纬度：{{latitude}}</div>
      <div>地址：{{address}}</div>
    </div>
    <baidu-map
      class="map"
      ak="PDpRVoNGsggN4k5kg4C8Gywc7KFEzvZ4"
      :zoom = "15"
      :scroll-wheel-zoom = "true"
      :center="{lng: longitude, lat: latitude}"
      @click = "handlerClickMap"
      @ready = "handlerMapReady"
    >
      <!--  定位    -->
      <bm-geolocation
        anchor="BMAP_ANCHOR_BOTTOM_RIGHT"
        :showAddressBar="true"
        :autoLocation="true"
        @locationSuccess = "handlerLocationSuccess"
        @locationError = "handlerLocationError"
      />
      <!--  点    -->
      <bm-marker
        :position="{lng: longitude, lat: latitude}"
        :dragging="true"
        animation="BMAP_ANIMATION_BOUNCE">
      </bm-marker>
      <!--  搜索    -->
      <bm-view class="map"></bm-view>
      <bm-control :offset="{width: '10px', height: '10px'}">
        <bm-auto-complete v-model="keyword" :sugStyle="{zIndex: 1}">
          <el-input style="margin: 5px 10px;" v-model="keyword" placeholder="请输入关键字"></el-input> <!-- 这里指代一个自定义搜索框组件 -->
        </bm-auto-complete>
      </bm-control>
      <bm-local-search :keyword="keyword" :auto-viewport="true" ></bm-local-search>
    </baidu-map>
  </div>
</template>
<script>
import BaiduMap from 'vue-baidu-map/components/map/Map.vue'
import BmGeolocation from 'vue-baidu-map/components/controls/Geolocation.vue'
import BmMarker from 'vue-baidu-map/components/overlays/Marker.vue'
import BmView from 'vue-baidu-map/components/map/MapView.vue'
import BmControl from 'vue-baidu-map/components/controls/Control.vue'
import BmAutoComplete from 'vue-baidu-map/components/others/AutoComplete.vue'
import BmLocalSearch from 'vue-baidu-map/components/search/LocalSearch.vue'

export default {
  name: 'Map',
  components: {
    BaiduMap,
    BmGeolocation,
    BmMarker,
    BmView,
    BmControl,
    BmAutoComplete,
    BmLocalSearch
  },
  data () {
    return {
      longitude: '113.355922',
      latitude: '23.184163',
      address: '广东省广州市天河区天源路5号-临',
      keyword: '',
      BMap: null
    }
  },
  mounted () {
  },
  methods: {
    /**
     * 通过ready 事件中使用返回的 BMap 类和 map 实例进行二次开发或手动控制其子组件
     */
    handlerMapReady ({ BMap, map }) {
      // console.log('BMap---------:', BMap)
      // console.log('map---------:', map)
      this.BMap = BMap
      // 获取当前定位
      this.getCurrentPosition()
    },
    /** 点击地图
     * const obj = {type, target, point, pixel, overlay}
     * @param obj
     */
    handlerClickMap (obj) {
      // console.log('target:', obj.target)
      this.longitude = obj.point.lng
      this.latitude = obj.point.lat
      // console.log('pixel:', obj.pixel)
      // console.log('overlay:', obj.overlay)
      this.getAddressByPoint(obj.point).then(addr => {
        // console.log('addr:', addr)
        this.address = addr
      })
    },
    /**
     * 处理定位成功
     * {point, addressComponent, marker}
     */
    handlerLocationSuccess (obj) {
      // console.log('=====obj:', obj)
      this.longitude = obj.point.lng
      this.latitude = obj.point.lat
      // console.log('addressComponent:', obj.addressComponent)
      // console.log('marker:', obj.marker)
      this.getAddressByPoint(obj.point).then(addr => {
        // console.log('addr:', addr)
        this.address = addr
      })
    },
    /**
     * 处理定位失败
     * {StatusCode}
     */
    handlerLocationError (obj) {
      // console.log(obj.statusCode)
    },
    /**
     * 根据经纬度获取地址
     * @param point
     */
    getAddressByPoint (point) {
      return new Promise(resolve => {
        const { BMap } = this
        /* 创建地址解析器的实例 */
        let geoCoder = new BMap.Geocoder()
        /* 利用坐标获取地址的详细信息 */
        geoCoder.getLocation(point, res => {
          resolve(res.address)
        })
      })
    },
    /**
     * 获取当前定位
     */
    getCurrentPosition () {
      const { BMap } = this
      let geolocation = new BMap.Geolocation()
      geolocation.enableSDKLocation() // 允许SDK辅助
      let _this = this
      geolocation.getCurrentPosition(function (result) { // 获取当前定位
        // 获取当前定位
        if (this.getStatus() === 0) {
          // console.log('result:', result)
          _this.longitude = result.longitude
          _this.latitude = result.latitude
          let point = new BMap.Point(result.longitude, result.latitude)
          _this.getAddressByPoint(point).then(addr => {
            // console.log('addr:', addr)
            _this.address = addr
          })
        }
      })
    }
  }
}
</script>

<style lang = "scss" scoped>
.test-container{
  /* 地图容器必须设置宽和高属性 */
  .map {
    width: 100%;
    height: 500px;
  }
}

</style>
